"use strict";
const App_1 = require("./library/App");
const core_1 = require("./library/core/core");
const input_1 = require("./library/core/input");
const postProcess_1 = require("./library/core/postProcess");
const texture2d_1 = require("./library/textures/texture2d");
const program_1 = require("./library/core/program");
const programManager_1 = require("./library/resources/programManager");
const resourceMap_1 = require("./library/resources/resourceMap");
const loaders_1 = require("./library/resources/loaders");
const timer_1 = require("./library/extras/timer");
const pointLight_1 = require("./library/lights/pointLight");
const vector3_1 = require("./library/maths/vector3");
const _demoCamera_1 = require("./library/_demoCamera");
const sampler_1 = require("./library/extras/sampler");
const ProgramCte_1 = require("./library/constants/ProgramCte");
const TextureType_1 = require("./library/constants/TextureType");
"use strict";
let camera = new _demoCamera_1.default(new Float32Array([-2.7, -1.4, 11.8]));
let SimpleConfig = function () {
    return {
        max: 10,
        resume: true,
        render: "0"
    };
};
let view;
let projection;
let tex2d;
let _light = new pointLight_1.default(new vector3_1.default(-5.0, 0.0, 0.0));
let identityMatrix = mat4.create();
mat4.identity(identityMatrix);
let angle = 0;
let text = SimpleConfig();
function loadAssets() {
    loaders_1.default.loadImage("descarga (4).png", "exampleImg");
    loaders_1.default.loadImage("assets/images/canyon/back.jpg");
    loaders_1.default.loadImage("assets/images/canyon/bottom.jpg");
    loaders_1.default.loadImage("assets/images/canyon/front.jpg");
    loaders_1.default.loadImage("assets/images/canyon/left.jpg");
    loaders_1.default.loadImage("assets/images/canyon/right.jpg");
    loaders_1.default.loadImage("assets/images/canyon/top.jpg");
}
const mainShader = "pp";
function initialize(app) {
    programManager_1.default.addWithFun("pp", () => {
        let prog2 = new program_1.default();
        prog2.addShader(`#version 300 es
            precision highp float;
            layout(location = 0) in vec3 vertPosition;
            uniform float tcdiv;
            out vec2 texCoord;
            void main(void) {
                texCoord = vec2(vertPosition.xy * 0.5) + vec2(0.5);
                //texCoord.x *= tcdiv / 5.0;
                //texCoord.y *= tcdiv / 5.0;
                gl_Position = vec4(vertPosition, 1.0);
            }`, ProgramCte_1.default.shader_type.vertex, ProgramCte_1.default.mode.read_text);
        prog2.addShader(`#version 300 es
            precision highp float;
            uniform sampler2D dataTexture;

            out vec4 fragColor;
            in vec2 texCoord;

            uniform float tcdiv;

            void main() {

                if(length(texCoord - 0.5) > 0.5){
                    discard;
                }
                vec2 tc = texCoord * vec2(tcdiv / 5.0);
                //fragColor = vec4(texCoord, 0.0, 1.0);
                fragColor = vec4(texture(dataTexture, tc).rgb, 1.0);
            }`, ProgramCte_1.default.shader_type.fragment, ProgramCte_1.default.mode.read_text);
        prog2.compile();
        prog2.addUniforms(["tcdiv"]);
        console.log(prog2);
        return prog2;
    });
    let cubeImage = resourceMap_1.default.retrieveAsset("exampleImg");
    tex2d = new texture2d_1.default(cubeImage, {
        flipY: true,
        minFilter: TextureType_1.default.Linear,
        magFilter: TextureType_1.default.Linear,
        wrapS: TextureType_1.default.Clamp2Edge,
        wrapT: TextureType_1.default.Clamp2Edge
    });
    const gl = core_1.default.getInstance().getGL();
    samplerA = new sampler_1.Sampler();
    samplerA.setParams({
        minFilter: gl.NEAREST,
        magFilter: gl.NEAREST,
        wrapS: gl.CLAMP_TO_EDGE,
        wrapT: gl.CLAMP_TO_EDGE,
    });
    samplerB = new sampler_1.Sampler();
    samplerB.setParams({
        minFilter: gl.LINEAR,
        magFilter: gl.LINEAR,
        wrapS: gl.REPEAT,
        wrapT: gl.REPEAT
    });
    samplerC = new sampler_1.Sampler();
    samplerC.setParams({
        minFilter: gl.NEAREST,
        magFilter: gl.LINEAR,
        wrapS: gl.MIRRORED_REPEAT,
        wrapT: gl.MIRRORED_REPEAT,
    });
    cameraUpdateCb();
}
;
let samplerA;
let samplerB;
let samplerC;
function drawScene(app) {
    core_1.default.getInstance().clearColorAndDepth();
    let prog = programManager_1.default.get(mainShader);
    prog.use();
    tex2d.bind(0);
    const renderMode = text.render;
    let mode;
    switch (renderMode) {
        case "0":
            mode = samplerA;
            break;
        case "1":
            mode = samplerB;
            break;
        case "2":
            mode = samplerC;
            break;
    }
    mode.bind(0);
    prog.sendUniform1i("texSampler", 0);
    prog.sendUniform1f("tcdiv", text.max);
    postProcess_1.default.bind();
    postProcess_1.default.render();
}
function cameraUpdateCb() {
    let canvas = core_1.default.getInstance().canvas();
    view = camera.GetViewMatrix();
    projection = camera.GetProjectionMatrix(canvas.width, canvas.height);
    let prog = programManager_1.default.get(mainShader);
    prog.use();
    prog.sendUniformMat4("view", view);
    prog.sendUniformMat4("projection", projection);
    prog.sendUniformVec3("viewPos", camera.position);
}
function updateScene(app, dt) {
    if (input_1.default.getInstance().isButtonClicked(input_1.default.mouseButton.Left)) {
        console.log("Mouse left clicked");
    }
    camera.timeElapsed = timer_1.default.deltaTime() / 10.0;
    camera.update(cameraUpdateCb);
    angle += timer_1.default.deltaTime() * 0.001;
}
;
window.onload = () => {
    new App_1.default({
        webglVersion: 2,
        loadAssets: loadAssets,
        initialize: initialize,
        update: updateScene,
        draw: drawScene,
        cameraUpdate: cameraUpdateCb,
        textCB: function (gui) {
            gui.add(text, "max", 5, 100);
            gui.add(text, "render", {
                simple: 0,
                lines: 1,
                points: 2
            });
        }
    }, text).start();
};



